#app3{
  display: flex;
  align-items: center;
 
}
#app3 .square{
  margin-left: 150px;
  border: 1px solid red;
  width: 10vw;
  height: 10vh;
  transition: 1s linear;
  caret-color: transparent;
  /* caret-color:transparent 去除点击组件里的光标 */
}
#app3 .square.active{
transform:translateX(15vw)
}